// Opacity used in dashboard icons
.opacity-25 {
  opacity: 0.25;
}
.opacity-50 {
  opacity: 0.5;
}
.opacity-75 {
  opacity: 0.75;
}
.opacity-85 {
  opacity: 0.85;
}
.tooltip {
  line-height: 1.3;
  font-weight: $font-weight-semi-bold;
}
