/*-----------------------------------------------
|   Data table
-----------------------------------------------*/

.dataTables_wrapper {
  .table-dashboard {
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }
  .table-responsive {
    margin-bottom: map_get($spacers, 3);
  }
  .dataTables_info {
    padding-top: 0 !important;
    font-size: map_get($font-sizes, "-1");
  }
  .data-table.dataTable {
    border-collapse: collapse !important;
    td,
    th {
      white-space: nowrap;
    }
  }
  .dataTables_filter input {
    max-width: 15rem;
  }
  .dataTable.dtr-inline.collapsed {
    > tbody > tr[role="row"] {
      > td {
        &:first-child,
        &:last-child {
          padding-left: 3.125rem !important;
        }
      }
      > td,
      > th {
        &:first-child:before {
          left: 1.25rem !important;
        }
      }
    }
  }

  .paging_simple {
    .page-link {
      border-radius: $border-radius !important;
      margin-left: map-get($spacers, 2) !important;
      background-color: $primary;
      padding-left: map-get($spacers, 4) !important;
      padding-right: map-get($spacers, 4) !important;
      border: 0;
      color: #fff;
      &:hover {
        background-color: darken($primary, 7.5%);
      }
    }
    .pagination {
      margin-top: 0 !important;
      margin-bottom: 0 !important;
    }
    .dataTables_wrapper {
      .dataTables_info {
        padding-top: 0 !important;
        text-align: left;
      }
    }
  }
  .disabled {
    .page-link {
      background-color: $light !important;
      color: $gray-600;
    }
  }

  .dtr-details {
    margin-left: 1.875rem !important;
  }
  .sort {
    padding-right: map-get($spacers, 1) !important;
    &:before {
      display: none !important;
    }
    &:after {
      position: relative !important;
      display: inline !important;
      top: 1px !important;
      bottom: auto !important;
      margin-left: 0.75rem !important;
      opacity: 1 !important;
    }
    &.sorting {
      &:after {
        content: url("data:image/png;base64,");
      }
    }
    &.sorting_asc {
      &:after {
        content: url("data:image/png;base64,") !important;
        top: -2px !important;
      }
    }
    &.sorting_desc {
      &:after {
        content: url("data:image/png;base64,") !important;
        top: -2px !important;
      }
    }
  }

  .no-sort {
    padding-right: map-get($spacers, 1) !important;
    &:before,
    &:after {
      display: none !important;
    }
  }
}
.data-table-pagination-sm {
  .paging_simple {
    .page-link {
      padding-left: map-get($spacers, 3) !important;
      padding-right: map-get($spacers, 3) !important;
      background-color: $gray-200 !important;
      color: $gray-500 !important;
      @include hover-focus {
        background-color: $gray-300 !important;
        color: $gray-600 !important;
      }
    }
    .disabled {
      .page-link {
        background-color: $light !important;
      }
    }
  }
}
.falcon-data-table {
  .data-table {
    margin: 0 !important;
  }
  .paging_full_numbers,
  .paging_first_last_numbers,
  .paging_full,
  .paging_numbers,
  .paging_simple_numbers {
    .page-link {
      @extend .btn;
      @extend .btn-falcon-default;
      margin-left: map-get($spacers, 1);
      margin-right: map-get($spacers, 1);
      padding: 3px 12px;
      border-radius: $border-radius !important;
      border: 1px solid $white !important;
    }
    .page-item.active {
      .page-link {
        background-color: transparent;
        color: $primary !important;
      }
    }
  }
}

table.dataTable.dtr-inline.collapsed
  > tbody
  > tr[role="row"]
  > td:first-child:before,
table.dataTable.dtr-inline.collapsed
  > tbody
  > tr[role="row"]
  > th:first-child:before {
  top: 0.3125rem;
}
.data-table-row-bulk-select {
  width: 1.75rem !important;
}
.data-table-row-action {
  width: 2.5rem !important;
}
table.dataTable.table-sm > thead > tr > th :not(.sorting_disabled) {
  padding-right: 0;
}
